@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Prociono&display=swap');

body {
  background-color: #333;
  color: #fff;
  font-family: 'Prociono', sans-serif;
}

.title {
  margin: 100px;
  margin-bottom: 150px;
  font-size: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 2px solid white;
  padding: 5px;
}

.skills-icons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.logo {
  width: 100px;
  margin: 20px;
  margin-bottom: 30px;
  transition: transform 0.2s ease-out;
}

.logo:hover {
  transform: scale(1.1) translateY(-14px);
}

.hover-effect {
  position: relative;
  display: inline-block;

}

.text {
  visibility: hidden;
  width: 100%;
  position: absolute;
  bottom: -30px;
  left: 0;
  text-align: center;
  background-color: #333;
  color: #fff;
  padding: 5px 0;
  transform: translateY(-30px);
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}

.hover-effect:hover .text {
  visibility: visible;
  opacity: 1;
}

.sidebar {
  height: 100px;
  display: flex;
  background-color: #333;
  box-shadow: 0 0 20px 3px;
}

.sidebar > svg {
  position: fixed;
  color: #fff;
  width: 30px;
  height: 30px;
  margin-top: 32px;
  margin-left: 32px;
  cursor: pointer;
}